body {
  margin: 0;
  min-height: 100vh;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;

  color: white;
  background-color: #000929;
}

.title {
  font-size: 36px;
  text-align: center;
}

.form,
.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;

  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  background: rgba(197, 197, 197, 0.6);

  padding: 24px;
  border-radius: 12px;
}

.container {
  background-color: transparent;
}

.inputContainer {
  margin: 6px;

  display: flex;
  align-items: center;
  flex-direction: column;
}

.input {
  border: none;
  outline: none;
  text-align: center;

  font-size: 18px;
  padding: 12px 18px;
  border-radius: 6px;
}

.btnContainear {
  margin: 12px;
}

.btn {
  border: none;
  outline: none;
  cursor: pointer;

  font-size: 16px;
  padding: 12px 18px;
  border-radius: 6px;

  font-weight: bold;
  text-transform: uppercase;
}

.btn:hover {
  color: white;
  background-color: #ff062e;
}

.error {
  color: black;
  font-weight: bold;
}

.link {
  cursor: pointer;
  margin-top: 12px;
}

.link:hover {
  color: #000929;
}

::-webkit-input-placeholder {
  text-align: center;
}

:-moz-placeholder {
  text-align: center;
}
